import React from 'react'
// 圖
import Logo from '../assets/img/logo.png'
import food1 from '../assets/img/food1.png'
import food2 from '../assets/img/food2.png'
// 元件
import Notice from './Notice'
import Product from './Product'
import Buy from './Buy'
// 套件元件
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons'

class Home extends React.Component{
    constructor(){
        super()
        // 預設
        this.state={
          noticeAction:'close',
          myCartAction:'close',
          data:[
              {id:1,name:'甜糰子',img:food1,price:'110',text:'日本甜糰子，好吃不膩口，彈牙美味'},
              {id:2,name:'熱湯粥',img:food2,price:'150',text:'熱湯粥，料多豐富，鮮美湯頭，暖意泉湧'}
          ],
          buy:[],
          total:0,
          fare: 100,
          num:1,
          num2:1,
          buy1:false,
          buy2:false
        }
    }

    handleMyCartClose=()=>{
        this.setState({myCartAction:'close'})
     }
    handleMyCartOpen=()=>{
       this.setState({myCartAction:'open'})
    }
    // 購買商品添加至購物車
    handleAddProduct=(e)=>{

        e.stopPropagation()
        // 取購物商品id
        let id = e.target.parentNode.parentNode.getAttribute('data-id')
        // 判別該商品是否已在buy陣列裡
        if(this.state.buy.indexOf(+id) === -1){
            this.setState({buy:[...this.state.buy,+id]})
        }
        if(this.state.myCartAction==='close'){
            this.handleMyCartOpen()
        }
        // 個商品購買開啟
        if(+id===1){
          this.setState({buy1:true})
        }else{
          this.setState({buy2:true})
        }
           
    }
    handleNotice=()=>{
        if(this.state.noticeAction==='active'){
          this.setState({noticeAction:'close'})
        }else{
          this.setState({noticeAction:'active'}) 
        }  
    }
    // 價錢總計算
    total=()=>{
        let spend1=0,spend2=0
      if(this.state.buy1){
         spend1 = +(this.state.num)*+(this.state.data[0].price)
      }
      if(this.state.buy2){
         spend2 = +(this.state.num2)*+(this.state.data[1].price)
      }
        return (+spend1+spend2)
    }
    // 購買商品數目增減
    handleAdd=(vaule,id)=>{
        if(id===1){
            if(this.state.num<1){
                this.setState({num:this.state.num})
            }else{
                this.setState({num:this.state.num+vaule})
            }
        }else if(id===2){
            if(this.state.num2<1){
                this.setState({num2:this.state.num2})
            }else{
                this.setState({num2:this.state.num2+vaule})
            }
        }    
    }
    // 移除購物車商品
    handleDelete=(vaule,e)=>{
        //  商品在buy陣列裡的位子，並移除
        let witch = this.state.buy.indexOf(vaule)
        let nowBuy = this.state.buy
        nowBuy.splice(witch,1)
        this.setState({buy:nowBuy})

        // 商品購物關閉並購買數量改回預設值1
        if(+vaule===1){
            this.setState({buy1:false})
            this.setState({num:1})    
          }else{
            this.setState({buy2:false})
            this.setState({num2:1})    
          }
        
    }
    render(){
        return(
            <>
              <div id="wrap">
                <header>
                    <img src={Logo} alt="logo" id="logo"/>
                    <FontAwesomeIcon icon={faShoppingCart} className="myCart pointer" onClick={this.handleMyCartOpen}/>
                </header>
                <section>
                    <div className="goods">
                    {this.state.data.map((vaule,index)=>{
                        return(
                            <Product
                               key={vaule.id}
                               id={vaule.id}
                               name={vaule.name}
                               img={vaule.img}
                               price={vaule.price}
                               text={vaule.text}
                               buy={this.state.buy}
                               onClick={this.handleAddProduct} 
                            />                 
                            )
                    })}
                        
                    </div>
                </section>
                <aside className={(this.state.myCartAction==='open')?'':'cartHide'}>
                  <div className="cartPart1">
                      <span>你的購物車</span>
                      <span className="pointer" onClick={this.handleMyCartClose}>X</span>
                  </div>
                  <div className="cartPart2">
                      <div className="buyList">
                        {/* {(this.state.buy.length === 0)?'': this.state.buy.map((vaule,index)=>{return <Buy key={vaule} id={vaule} name={this.state.data[vaule-1].name} price={this.state.data[vaule-1].price} img={this.state.data[vaule-1].img} num={this.state.num} num2={this.state.num2} onClick={this.handleAdd} delete={this.handleDelete}/>})} */}
                         <Buy
                          key={1} id={1} 
                          name={this.state.data[0].name} 
                          price={this.state.data[0].price} 
                          img={this.state.data[0].img} 
                          num={this.state.num}
                          buy={this.state.buy1}  
                          onClick={this.handleAdd} 
                          delete={this.handleDelete}    
                         />
                         <Buy
                          key={2} id={2} 
                          name={this.state.data[1].name} 
                          price={this.state.data[1].price} 
                          img={this.state.data[1].img} 
                          num={this.state.num2}
                          buy={this.state.buy2}    
                          onClick={this.handleAdd} 
                          delete={this.handleDelete}    
                         />
                      </div>
                      <div className="count">
                        <div className="ps">
                            <p>
                                注意:<br/>
                                若商品有瑕疵，請保存並與公司聯繫。
                            </p>
                            <div>購物滿NT300免運費</div>
                        </div>
                        <div className={(this.state.buy.length === 0)?'hide':''}>
                            <div className={(this.total()>=300)?"spend hide":"spend"}><span>運費</span><span>NT{this.state.fare}</span></div>
                            <div className="spend"><span>總計</span><span>NT{(this.total()>=300)?this.total():this.total()+this.state.fare}</span></div>
                        </div>
                      </div>
                  </div>
                  <div className={(this.state.buy.length === 0)?'cartPart3 canttouch gray':'cartPart3 pointer'} onClick={this.handleNotice}>
                      <span>結</span>帳
                  </div>
                </aside>
              
              </div>
              {/* 購買完成通知 */}
              <Notice 
                  noticeAction={this.state.noticeAction}
                  onClick={this.handleNotice}
              />
            </>
        )
    }
}

export default Home;