import React from 'react'

class Buy extends React.Component{
    constructor(){
        super()
        this.state={
        }
    }
    render(){
        return(
            <>
                <div className={(this.props.buy)?"buy":"buy hide"}>
                    <div className="buyPart1">
                        <img src={this.props.img} alt=""/>
                    </div>
                    <div className="buyPart2">
                        <span>-{this.props.name}-</span>
                        NT<i>{this.props.price}</i>
                        <div className="quantity">
                            <span 
                            className="pointer" 
                            onClick={(this.props.num === 1 || this.props.num2 === 1)?()=>{}:()=>this.props.onClick(-1,this.props.id)}>-</span>
                            <i>{this.props.num}</i>
                            <span 
                            className="pointer" 
                            onClick={()=>this.props.onClick(+1,this.props.id)}>+</span>
                        </div>
                    </div>
                    {this.props.counter}
                    <div 
                    className="buyPart3 pointer" 
                    onClick={(e)=>this.props.delete(this.props.id,e)}>X</div>
                </div>

                {/* <div className={(this.state.buy1)?"buy":"buy hide"} key="1">
                                <div className="buyPart1">
                                    <img src={this.state.data[0].img} alt=""/>
                                </div>
                                <div className="buyPart2">
                                    <span>-{this.state.data[0].name}-</span>
                                    NT<i>{this.state.data[0].price}</i>
                                    <div className="quantity">
                                        <span 
                                        className="pointer" 
                                        onClick={(this.state.num === 1 )?()=>{}:()=>this.handleAdd(-1,1)}>-</span>
                                        <i>{this.state.num}</i>
                                        <span 
                                        className="pointer" 
                                        onClick={()=>this.handleAdd(+1,1)}>+</span>
                                    </div>
                                </div>
                                <div 
                                className="buyPart3 pointer" 
                                onClick={(e)=>this.handleDelete(1,e)}>X</div> */}
            </>
        )
    }
}

export default Buy;