import React from 'react';
import ok from '../assets/img/ok.png'

function Notice (props){
    return(
        <>
            <div id="notice" className={(props.noticeAction==='active')?'':'hide'}>
                <div className="noticeCard">
                    <img src={ok} alt="" />
                    <h1>-結帳完成-</h1>
                    <span className="pointer" onClick={props.onClick}>確認</span>
                </div>
            </div>
        </>
    )
    
}

export default Notice;